.printers table {
  padding: 0px;
  margin: 10px;
  display: inline-block;
  border-spacing: 0px;
}

.printers td {
  border: 1px solid;
  padding: 3px 0px 0px 0px;
  margin: 0px;
}

.printers tr {
  padding: 0px;
  margin: 0px;
  display: flex;
}

.printers ul {
  list-style: none;
  margin: 0px;
  padding: 0px;
  text-align: left;
  display: inline-block;
}

.printers li {
  text-align: center;
  padding: 0px;
  margin: 0px;
}

